import React, { useState, useEffect, memo } from 'react';
import { Row, Card, CardTitle, Label, FormGroup, Button } from 'reactstrap';
import { NavLink } from 'react-router-dom';
import { Formik, Form, Field } from 'formik';
import { connect } from 'react-redux';
import Countdown, { zeroPad } from 'react-countdown';

import { Colxx } from '../../components/common/CustomBootstrap';
import IntlMessages from '../../helpers/IntlMessages';
import { verifyOTP } from '../../redux/actions';
import { NotificationManager } from '../../components/common/react-notifications';
import { getAllowOTP } from '../../helpers/Utils';
import useRequest from '../../hooks/use-request';

const validateOTP = (value) => {
  let error;
  if (!value) {
    error = 'Please enter an OTP';
  } else if (value.length !== 4) {
    error = 'Please enter a valid OTP!';
  }
  return error;
};

const ResendButton = () => {
  const { email } = getAllowOTP();
  const [doRequest] = useRequest({
    url: '/auth/resend-otp',
    method: 'POST',
    data: {
      email,
    },
    nextSuccess: () => {
      NotificationManager.success(
        'OTP reset Successful!',
        'Verify OTP Success',
        3000,
        null,
        null,
        ''
      );
    },
  });
  const onClick = () => doRequest();
  const renderer = ({ hours, minutes, seconds, completed }) => {
    // Render a countdown
    return (
      <Button
        className="btn-shadow btn btn-primary btn-lg"
        onClick={completed ? onClick : () => {}}
        disabled={!completed}
      >
        {completed
          ? 'Resend OTP'
          : `${zeroPad(hours)}:${zeroPad(minutes)}:${zeroPad(seconds)}`}
      </Button>
    );
  };

  return <Countdown date={Date.now() + 300000} renderer={renderer} />;
};

const MemoizedResendOTPButton = memo(ResendButton);

const VerifyOTPPage = ({
  history,
  loading,
  error,
  message,
  verifyOTPAction,
}) => {
  const [otp] = useState('');

  const { active, email } = getAllowOTP();

  const onVerifyOTP = (values) => {
    if (loading) {
      return;
    }
    if (email === undefined) {
      NotificationManager.error(
        'Invalid Request!',
        'Verify OTP Error',
        3000,
        null,
        null,
        ''
      );
      history.push('/');
      return;
    }
    if (email !== '' && values.otp !== '') {
      verifyOTPAction({ ...values, email }, history);
    }
  };

  useEffect(() => {
    if (error) {
      NotificationManager.error(
        error,
        'Verify OTP Error',
        3000,
        null,
        null,
        ''
      );
    } else if (!loading && message === 'logged_in')
      NotificationManager.success(
        'Login Successful!',
        'Verify OTP Success',
        3000,
        null,
        null,
        ''
      );
  }, [error, message, loading]);

  const initialValues = { otp };

  if (!active) {
    history.push('/');
  }

  return (
    <Row className="h-100">
      <Colxx xxs="12" md="10" className="mx-auto my-auto">
        <Card className="auth-card">
          <div className="position-relative image-side " />
          <div className="form-side">
            <NavLink to="/" className="white">
              {/* <span className="logo-single" /> */}
            </NavLink>
            <CardTitle className="mb-4">
              <h1 className="mb-4"> FUGATAUA CRM PORTAL </h1> <br />
              <IntlMessages id="user.verify-otp" />
            </CardTitle>

            <Formik initialValues={initialValues} onSubmit={onVerifyOTP}>
              {({ errors, touched }) => (
                <Form className="av-tooltip tooltip-label-bottom">
                  <FormGroup className="form-group has-float-label">
                    <Label>
                      <IntlMessages id="user.enter-otp" />
                    </Label>
                    <Field
                      className="form-control"
                      autoFocus
                      name="otp"
                      validate={validateOTP}
                    />
                    {errors.otp && touched.otp && (
                      <div className="invalid-feedback d-block">
                        {errors.otp}
                      </div>
                    )}
                  </FormGroup>

                  <div className="d-flex justify-content-end align-items-center">
                    <MemoizedResendOTPButton />

                    <Button
                      color="primary"
                      className={`btn-shadow btn-multiple-state ml-2 ${
                        loading ? 'show-spinner' : ''
                      }`}
                      size="lg"
                      type="submit"
                    >
                      <span className="spinner d-inline-block">
                        <span className="bounce1" />
                        <span className="bounce2" />
                        <span className="bounce3" />
                      </span>
                      <span className="label">
                        <IntlMessages id="user.verify-otp-button" />
                      </span>
                    </Button>
                  </div>
                </Form>
              )}
            </Formik>
          </div>
        </Card>
      </Colxx>
    </Row>
  );
};

const mapStateToProps = ({ authUser }) => {
  const { loading, message, error } = authUser;
  return { loading, message, error };
};

export default connect(mapStateToProps, {
  verifyOTPAction: verifyOTP,
})(VerifyOTPPage);
